<template>
  <div>
    <!--<a href="javascript:history.go(-1) "><h1 class="goback"></h1></a>-->
    <p>SCORE:<span id="score">0</span></p>
    <div id="gameOver">
      <p>
        GAME OVER!<br>
        SCOR: <span id="final">0</span><br>
        <a class="btn" href="javascript:game.start();">TRY AGAIN!</a>
      </p>
    </div>
    <div id="gridPanel">
      <div id="c00" class="cell"></div>
      <div id="c01" class="cell"></div>
      <div id="c02" class="cell"></div>
      <div id="c03" class="cell"></div>

      <div id="c10" class="cell"></div>
      <div id="c11" class="cell"></div>
      <div id="c12" class="cell"></div>
      <div id="c13" class="cell"></div>

      <div id="c20" class="cell"></div>
      <div id="c21" class="cell"></div>
      <div id="c22" class="cell"></div>
      <div id="c23" class="cell"></div>

      <div id="c30" class="cell"></div>
      <div id="c31" class="cell"></div>
      <div id="c32" class="cell"></div>
      <div id="c33" class="cell"></div>
    </div>
  </div>
</template>

<script>
  export default {
    methods:{
      gotogame(){
        var game = {
          data: null, RN: 4, CN: 4,
          score: 0,
          state: 1,//1  0 
          RUNNING: 1, //
          GAMEOVER: 0, //
          start() {
            this.state = this.RUNNING;
            this.score = 0;
            this.data = [];
            for (var r = 0; r < this.RN; r++) {
              this.data[r] = [];
              for (var c = 0; c < this.CN; c++) {
                this.data[r][c] = 0;
              }
            }
            this.randomNum();
            this.randomNum();
            this.updateView();
            document.onkeydown = function (e) {
              switch (e.keyCode) {
                case 37:
                  this.moveLeft();
                  break;
                case 38:
                  this.moveUp();
                  break;
                case 39:
                  this.moveRight();
                  break;
                case 40:
                  this.moveDown();
                  break;
              }
            }.bind(this);
          },
          isGameOver() {
            for (var r = 0; r < this.RN; r++) {
              for (var c = 0; c < this.CN; c++) {
                if (this.data[r][c] == 0) {
                  return false;
                } else if (c < this.CN - 1 && (this.data[r][c] = this.data[r][c + 1])) {
                  return false;
                } else if (r < this.RN - 1 && (this.data[r][c] = this.data[r + 1][c])) {
                  return false;
                }
              }
              return true;
            }
          },
          moveRight() {
            var before = String(this.data);
            for (var r = 0; r < 4; r++)
              this.moveRightInRow(r);
            var after = String(this.data);
            if (before != after) {
              this.randomNum();
              this.updateView();
            }
            ;
          },
          moveRightInRow(r) {
            for (var c = this.CN - 1; c > 0; c--) {
              var prevc = this.getPrevcInRow(r, c)
              if (prevc == -1) {
                break;
              } else {
                if (this.data[r][c] == 0) {
                  this.data[r][c] = this.data[r][prevc];
                  this.data[r][prevc] = 0;
                  c++;
                } else if (this.data[r][c] == this.data[r][prevc]) {
                  this.data[r][c] *= 2;
                  this.score += this.data[r][c];
                  this.data[r][prevc] = 0;
                }
              }
            }
          },
          getPrevcInRow(r, c) {
            for (var i = c - 1; i >= 0; i--) {
              if (this.data[r][i] != 0) {
                return i;
              }
            }
            return -1;
          },
          moveLeft() {
            var befor = String(this.data);
            for (var r = 0; r < this.RN; r++) {
              this.moveLeftInRow(r)
            }
            var after = String(this.data);
            if (befor != after) {
              this.randomNum()
              this.updateView()
            }
          },
          moveLeftInRow(r) {
            for (var c = 0; c < this.CN - 1; c++) {
              var nextc = this.getNextInRow(r, c)
              if (nextc == -1) {
                break;
              } else {
                if (this.data[r][c] == 0) {
                  this.data[r][c] = this.data[r][nextc];
                  this.data[r][nextc] = 0;
                  c--;
                } else if (this.data[r][c] == this.data[r][nextc]) {
                  this.data[r][c] *= 2;
                  this.score += this.data[r][c];
                  this.data[r][nextc] = 0;
                }
              }
            }
          },
          getNextInRow(r, c) {
            for (var i = c + 1; i < this.CN; i++) {
              if (this.data[r][i] != 0) {
                return i;
              }
            }
            return -1;
          },
          moveUp() {
            var befor = String(this.data);
            for (var c = 0; c < this.CN; c++) {
              this.moveUpIncol(c)
            }
            var after = String(this.data);
            if (befor != after) {
              this.randomNum()
              this.updateView()
            }
          },
          moveUpIncol(c) {
            for (var r = 0; r < this.CN - 1; r++) {
              var nextr = this.getNextrInCol(r, c)
              if (nextr == -1) {
                break;
              } else {
                if (this.data[r][c] == 0) {
                  this.data[r][c] = this.data[nextr][c];
                  this.data[nextr][c] = 0;
                  r--;
                } else if (this.data[r][c] == this.data[nextr][c]) {
                  this.data[r][c] *= 2;
                  this.score += this.data[r][c];
                  this.data[nextr][c] = 0;
                }
              }
            }
          },
          getNextrInCol(r, c) {
            for (var i = r + 1; i < this.RN; i++) {
              if (this.data[i][c] != 0) {
                return i;
              }
            }
            return -1;
          },
          moveDown() {
            var befor = String(this.data);
            for (var c = 0; c < this.CN; c++) {
              this.moveDownIncol(c)
            }
            var after = String(this.data);
            if (befor != after) {
              this.randomNum()
              this.updateView()
            }
          },
          moveDownIncol(c) {
            for (var r = this.CN - 1; r >= 0; r--) {
              var prevr = this.getPrevrInCol(r, c)
              if (prevr == -1) {
                break;
              } else {
                if (this.data[r][c] == 0) {
                  this.data[r][c] = this.data[prevr][c];
                  this.data[prevr][c] = 0;
                  r++;
                } else if (this.data[r][c] == this.data[prevr][c]) {
                  this.data[r][c] *= 2;
                  this.score += this.data[r][c];
                  this.data[prevr][c] = 0;
                }
              }
            }
          },
          getPrevrInCol(r, c) {
            for (var i = r - 1; i >= 0; i--) {
              if (this.data[i][c] != 0) {
                return i;
              }
            }
            return -1;
          },
          updateView() {//html
            for (var r = 0; r < this.RN; r++) {
              for (var c = 0; c < this.CN; c++) {
                var n = this.data[r][c];
                var div = document.getElementById('c' + r + c);
                if (n != 0) {
                  div.innerHTML = n;
                  div.className = "cell n" + n;
                } else {
                  div.innerHTML = "";
                  div.className = "cell";
                }
              }
            }
            document.getElementById("score")
              .innerHTML = this.score;
            document.getElementById("gameOver")
              .style.display =
              this.state == this.GAMEOVER ?
                "block" : "none";
            this.state == this.GAMEOVER && (
              document.getElementById("final")
                .innerHTML = this.score)
          },
          randomNum() {//
            while (true) {
              var r = Math.floor(Math.random() * 4);
              var c = Math.floor(Math.random() * 4);
              if (this.data[r][c] == 0) {
                this.data[r][c] = Math.random() > 0.5 ? 2 : 4;
                break;
              }
            }
          },
        }
        game.start();
      }
    },
    mounted(){
      this.gotogame()
    }
  }
</script>

<style scoped>
  a{text-decoration: none;}
  .goback{
    text-align: center;
  }
  #gridPanel {
    width: 480px;
    height: 480px;
    margin: 0 auto;
    position: relative;
    background: #bbada0;
    border-radius: 10px;
  }

  .cell {
    width: 100px;
    height: 100px;
    position: absolute;
    border-radius: 6px;
    background: #ccc0b3;
    font-size: 60px;
    text-align: center;
    color: #fff;
    line-height: 100px;
  }

  [id^="c0"] {
    top: 16px
  }

  [id^="c1"] {
    top: 132px
  }

  [id^="c2"] {
    top: 248px
  }

  [id^="c3"] {
    top: 364px
  }

  [id$="0"] {
    left: 16px
  }

  [id$="1"] {
    left: 132px
  }

  [id$="2"] {
    left: 248px
  }

  [id$="3"] {
    left: 364px
  }

  .n2 {
    background-color: #eee3da
  }

  .n4 {
    background-color: #ede0c8
  }

  .n8 {
    background-color: #f2b179
  }

  .n16 {
    background-color: #f59563
  }

  .n32 {
    background-color: #f67c5f
  }

  .n64 {
    background-color: #f65e3b
  }

  .n128 {
    background-color: #edcf72
  }

  .n256 {
    background-color: #edcc61
  }

  .n512 {
    background-color: #9c0
  }

  .n1024 {
    background-color: #33b5e5
  }

  .n2048 {
    background-color: #09c
  }

  .n4096 {
    background-color: #a6c
  }

  .n8192 {
    background-color: #93c
  }

  .n2, .n4 {
    color: #776e65
  }

  .n1024, .n2048, .n4096, .n8192 {
    font-size: 40px
  }

  p {
    width: 480px;
    margin: 0 auto;
    font-size: 40px;
    font-family: Arial;
    font-weight: bold;
    padding-top: 15px;
  }

  #gameOver {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    right: 0px;
    bottom: 0px;
    background: rgba(55, 55, 55, .5)
  }

  #gameOver > p {
    width: 300px;
    height: 200px;
    background: #fff;
    position: absolute;
    top: 50%;
    left: 50%;
    text-align: center;
    line-height: 1.5em;
    border-radius: 10px;
    boeder: 1px solid #edcf72;
  }
</style>



