<template>
  <div class="container">
    <div id="five">
      <canvas id="can" width="600" height="600"></canvas>
    </div>
      <!--<div>-->
        <!--<h3></h3>-->
        <!--<ol>-->
          <!--<li></li>-->
          <!--<li></li>-->
        <!--</ol>-->
      <!--</div>-->
  </div>
</template>

<script>
  export default {
    mounted(){
      this.game()
    },
    methods:{
      game(){
        var maps = new Array(15);
        for (var i = 0; i < 15; i++) {
          maps[i] = new Array(15);
          for (var j = 0; j < 15; j++) {
            maps[i][j] = 0
          }
        }
        var can = document.getElementById("can");
        var ctx = can.getContext("2d");
        ctx.strokeStyle = "#fff";
        for (var i = 0; i < 15; i++) {
          for (var j = 0; j < 15; j++) {
            ctx.strokeRect(j * 40, i * 40, 40, 40)
          }
        }
        var black = new Image();
        var white = new Image();
        var isBlack = false;
        black.src =  require("../../static/images/five/black.png");
        white.src =  require("../../static/images/five/white.png");

        can.onclick = function (e) {
          var x = e.offsetX;
          var y = e.offsetY;
          if(x%40<=20){
            x -= x%40;
          }else{
            x += (40-x%40)
          }
          if(y%40<=20){
            y -= y%40;
          }else{
            y += (40-y%40)
          }
          var col = 0, row = 0;
          col = x/40; row= y/40;
          if(isBlack){
            ctx.drawImage(black,col*40-18,row*40-18);
            isBlack = false;
            maps[row][col]=2;
            isWin(2,row,col)
          }else{
            ctx.drawImage(white,col*40-18,row*40-18);
            isBlack = true;
            maps[row][col]=1;
            isWin(1,row,col)
          }
          //
          function sumTotal(t,total) {
            if(total>=5){
              if(t==1) alert('');
              else alert('');
            }
          }
          function isWin(t,row,col){
            var orRow = row , orCol = col , total = 1;
            //
            while (col-1>0 && maps[row][col-1]==t){
              total++;
              col--;
            }
            row = orRow; col=orCol;
            while(col+1<15 &&maps[row][col+1]==t){
              col++;
              total++;
            }
            sumTotal(t,total)
            //
            row = orRow; col=orCol; total = 1;
            while (row-1>0 && maps[row-1][col]==t){
              total++;
              row--;
            }
            row = orRow; col=orCol;
            while(row+1<15 &&maps[row+1][col]==t){
              row++;
              total++;
            }
            sumTotal(t,total)
            //
            row = orRow; col=orCol; total = 1;
            while (row-1>0&&col-1>0 && maps[row-1][col-1]==t){
              total++;
              row--;
              col--;
            }
            row = orRow; col=orCol;
            while(row+1<15&&col+1<15 &&maps[row+1][col+1]==t){
              row++;
              col++;
              total++;
            }
            sumTotal(t,total)
            //
            row = orRow; col=orCol; total = 1;
            while (row-1>0&&col+1<15 && maps[row-1][col+1]==t){
              total++;
              row--;
              col++;
            }
            row = orRow; col=orCol;
            while(row+1<15&&col-1>0 &&maps[row+1][col-1]==t){
              row++;
              col--;
              total++;
            }
            sumTotal(t,total)
          }
        }
      }
    }
  }
</script>

<style scoped>
#five{
  width: 600px;height: 600px;margin: 0px auto; border: 1px solid #000;
  background: url(../../static/images/five/bak.jpg)
}
</style>



