<script>
  export default {
    methods:{
      shopping(){
        $(function (){
          // 
          (function (){
            var aLi = $('#menu li');
            var oText = $('#search').find('.form .text');
            var arrText = [
              '  ',
              '2609',
              '',
              '',
              ''
            ];
            var iNow = 0;

            oText.val(arrText[iNow]);

            aLi.each(function ( index ){
              $(this).click(function (){
                aLi.attr('class', 'gradient');
                $(this).attr('class', 'active');

                iNow = index;

                oText.val(arrText[iNow]);
              });
            });

            oText.focus(function (){
              if( $(this).val() == arrText[iNow] ) {
                $(this).val('');
              }
            });
            oText.blur(function (){
              if( $(this).val() == '' ) {
                oText.val(arrText[iNow]);
              }
            });
          })();

          // update
          (function (){
            var oDiv = $('.update');
            var oUl = oDiv.find('ul');
            var iH = 0;
            var arrData = [
              { 'name':'', 'time':4, 'title':'', 'url':'http://www.miaov.com/2013/' },
              { 'name':'', 'time':5, 'title':'3', 'url':'http://www.miaov.com/2013/#curriculum' },
              { 'name':'', 'time':6, 'title':'', 'url':'http://www.miaov.com/2013/#about' },
              { 'name':'', 'time':7, 'title':'', 'url':'http://www.miaov.com/2013/#message' },
              { 'name':'', 'time':8, 'title':'', 'url':'http://www.miaov.com/2013/' },
              { 'name':'', 'time':9, 'title':'3', 'url':'http://www.miaov.com/2013/#curriculum' },
              { 'name':'', 'time':10, 'title':'', 'url':'http://www.miaov.com/2013/#about' },
              { 'name':'', 'time':11, 'title':'', 'url':'http://www.miaov.com/2013/#message' }
            ];
            var str = '';
            var oBtnUp = $('#updateUpBtn');
            var oBtnDown = $('#updateDownBtn');
            var iNow = 0;
            var timer = null;

            for ( var i=0; i<arrData.length; i++ ) {
              str += '<li><a href="'+ arrData[i].url +'"><strong>'+ arrData[i].name +'</strong> <span>'+ arrData[i].time +'</span> '+ arrData[i].title +'</a></li>';
            }
            oUl.html( str );

            iH = oUl.find('li').height();

            oBtnUp.click(function (){
              doMove(-1);
            });
            oBtnDown.click(function (){
              doMove(1);
            });

            oDiv.hover(function (){
              clearInterval( timer );
            }, autoPlay);

            function autoPlay() {
              timer = setInterval(function () {
                doMove(-1);
              }, 3500);
            }
            autoPlay();

            function doMove( num ) {
              iNow += num;
              if ( Math.abs(iNow) > arrData.length-1 ) {
                iNow = 0;
              }
              if ( iNow > 0 ) {
                iNow = -(arrData.length-1);
              }
              oUl.stop().animate({ 'top': iH*iNow }, 2200, 'elasticOut');
            }
          })();

          // options 
          (function (){

            fnTab( $('.tabNav1'), $('.tabCon1'), 'click' );
            fnTab( $('.tabNav2'), $('.tabCon2'), 'click' );
            fnTab( $('.tabNav3'), $('.tabCon3'), 'mouseover' );
            fnTab( $('.tabNav4'), $('.tabCon4'), 'mouseover' );

            function fnTab( oNav, aCon, sEvent ) {
              var aElem = oNav.children();
              aCon.hide().eq(0).show();

              aElem.each(function (index){

                $(this).on(sEvent, function (){
                  aElem.removeClass('active').addClass('gradient');
                  $(this).removeClass('gradient').addClass('active');
                  aElem.find('a').attr('class', 'triangle_down_gray');
                  $(this).find('a').attr('class', 'triangle_down_red');

                  aCon.hide().eq( index ).show();
                });

              });
            }
          })();

          // 
          (function (){
            var oDiv = $('#fade');
            var aUlLi = oDiv.find('ul li');
            var aOlLi = oDiv.find('ol li');
            var oP = oDiv.find('p');
            var arr = [ '~', '', '' ];
            var iNow = 0;
            var timer = null;

            fnFade();

            aOlLi.click(function (){
              iNow = $(this).index();
              fnFade();
            });
            oDiv.hover(function (){ clearInterval(timer); }, autoPlay);

            function autoPlay() {
              timer = setInterval(function () {
                iNow++;
                iNow%=arr.length;
                fnFade();
              }, 2000);
            }
            autoPlay();

            function fnFade() {
              aUlLi.each(function (i){
                if ( i != iNow ) {
                  aUlLi.eq(i).fadeOut().css('zIndex', 1);
                  aOlLi.eq(i).removeClass('active');

                } else {
                  aUlLi.eq(i).fadeIn().css('zIndex', 2);
                  aOlLi.eq(i).addClass('active');
                }
              });
              oP.text(arr[iNow]);
            }
          })();

          //
          (function (){
            var aSpan = $('.calendar h3 span');
            var aImg = $('.calendar .img');
            var oPrompt = $('.today_info');
            var oImg = oPrompt.find('img');
            var oStrong = oPrompt.find('strong');
            var oP = oPrompt.find('p');

            aImg.hover(function (){
              var iTop = $(this).parent().position().top - 30;
              var iLeft = $(this).parent().position().left + 55;
              var index = $(this).parent().index()%aSpan.size();

              // console.log( $(this).parent().index()%aSpan.size() );

              oPrompt.show().css({ 'left': iLeft, 'top': iTop });
              oP.text($(this).attr('info'));
              oImg.attr('src', $(this).attr('src'));
              oStrong.text( aSpan.eq(index).text() );


            }, function (){
              oPrompt.hide();
            });
          })();

          // BBS
          (function (){
            $('.bbs ol li').mouseover(function (){
              $('.bbs ol li').removeClass('active').eq($(this).index()).addClass('active');
            });
          })();

          // HOT
          (function (){
            var arr = [
              '',
              '1<br />1',
              '<br />CBD<br />124987',
              '3<br />3',
              '4<br />4',
              '5<br />5',
              '6<br />6',
              '7<br />7',
              '8<br />8',
              '9<br />9',
              '10<br />10'
            ];
            $('.hot_area li').mouseover(function (){

              if ( $(this).index() == 0 ) return;

              $('.hot_area li p').remove();

              $(this).append('<p style="width:'+ ($(this).width()-12) +'px; height:'+ ($(this).height()-12) +'px;">'+ arr[$(this).index()] +'</p>');
            });
          })();
        });
      }
    },
    mounted(){
      this.shopping
    }
  }
</script>

