  #search .update { width:405px; height:30px; position:absolute; left:226px; bottom:0; background:url(../../static/images/shopping/search_img.png) no-repeat -226px 8px; }
  #search .wrap { width:336px; height:30px; overflow:hidden; position:relative; left:60px; }
  #search .update ul { width:336px; position:absolute; top:0px; left:0; }
