  #search .triangle_down_red { top:17px; right:0; }

  .video_title { height:42px; line-height:42px; background:url(../../static/images/shopping/video_bg.gif) no-repeat 105px 10px; font-size:18px; font-family:arial; color:#d90000; font-weight:normal; position:relative; }
  .video_title sup { position:absolute; top:-10px; font-family:''; }
  .video { margin-bottom:13px; }
  .video_list { padding-left:75px; height:78px; background:url(../../static/images/shopping/video_bg.gif) no-repeat 3px -47px; }
  .video_list li { line-height:20px; text-indent:14px; background:url(../../static/images/shopping/dot.gif) no-repeat 5px center; }
