      showScroll() {
        let top1 = $(".section2").offset().top-30;
        let top2 = $(".section3").offset().top-30;
        let top3 = $(".section4").offset().top-30;
        let top4 = $(".section5").offset().top-30;
        let top5 = $(".section6").offset().top-30;
        let top6 = $(".section7").offset().top-30;
        let min_height = document.documentElement.clientHeight;
        $(window).scroll(function () {
          let s = $(window).scrollTop();
          s > min_height ? $('.gotop').show() : $('.gotop').hide();
          s > min_height ? $('.mynav').addClass("fixed") : $('.mynav').removeClass("fixed");
          if (s > top1 && s < top2) {
            $(".nav-bar li:nth-child(1)").addClass("active").siblings().removeClass("active")
          } else if (s > top2 && s < top3) {
            $(".nav-bar li:nth-child(2)").addClass("active").siblings().removeClass("active")
          } else if (s > top3 && s < top4) {
            $(".nav-bar li:nth-child(3)").addClass("active").siblings().removeClass("active")
          } else if (s > top4 && s < top5) {
            $(".nav-bar li:nth-child(4)").addClass("active").siblings().removeClass("active")
          } else if (s > top5 && s < top6) {
            $(".nav-bar li:nth-child(5)").addClass("active").siblings().removeClass("active")
          } else if (s > top6) {
            $(".nav-bar li:nth-child(6)").addClass("active").siblings().removeClass("active")
          }
        });

      },
      totop() {
        $('html,body').stop().animate({
          scrollTop: 0
        }, 700);
      },
      myWeixin() {
        this.myweixin = !this.myweixin
      },
      bgchange() {
        var length = 3;
        setInterval(() => {
          var randomBgIndex = Math.round(Math.random() * length);
          $(".section1 .imgBg li").eq(randomBgIndex).addClass("show").siblings().removeClass("show");
        }, 5000);
      },
      musicV() {
        let audio = document.getElementById("music")
        audio.volume = 0.2;
        audio.loop = true;
      },
      goto(num) {
        let top1 = $(".section1").offset().top;
        let top2 = $(".section2").offset().top;
        let top3 = $(".section3").offset().top;
        let top4 = $(".section4").offset().top;
        let top5 = $(".section5").offset().top;
        let top6 = $(".section6").offset().top;
        let top7 = $(".section7").offset().top;
        let tops = [top1, top2, top3, top4, top5, top6, top7];
        $('html,body').stop().animate({
          scrollTop: tops[num]
        }, 700);
      },
    },
    mounted() {
      this.bgchange();
      this.musicV();
      this.showScroll()
