import Vue from 'vue'
import VueRouter from 'vue-router'
import VueResource from 'vue-resource'
import App from './App'
import IndexPage from './pages/index'
import GamePage from './pages/game'
import IntelPage from './pages/intel'
import ShopCartPage from './pages/shopcart'
import FivePage from './pages/five'
import ShoppingPage from './pages/shopping'
import BusinessPage from './pages/business'
import ElemePage from './pages/eleme'
import MusicPage from './pages/music'
import Analysis from './components/business/analysis'
import Count from './components/business/count'
import Forecast from './components/business/forecast'
import Publish from './components/business/publish'
import MyBlogs from './pages/myblogs'
Vue.use(VueRouter)
Vue.use(VueResource)

let router = new VueRouter({
  // mode:'history',
  routes:[
    {
      path:'/', name:'IndexPage',component: IndexPage
    },
    {
      path:'/myblogs', name:'MyBlogs',component: MyBlogs
    },
    {
      path:'/business',
      name:'BusinessPage',
      component:BusinessPage,
      redirect: '/business/count',
      children:[
        { path: 'analysis',name:'Analysis', component: Analysis},
        { path: 'count',name:'Count', component: Count},
        { path: 'forecast',name:'Forecast', component: Forecast},
        { path: 'publish',name:'Publish', component: Publish}
      ]
    },
    {
      path:'/eleme',name:'ElemePage', component:ElemePage
    },
    {
      path:'/music',name:'MusicPage', component:MusicPage
    },
    {
      path:'/game',name:'GamePage', component:GamePage
    },
    {
      path:'/intel',name:'IntelPage', component:IntelPage
    },
    {
      path:'/shopcart',name:'ShopCartPage', component:ShopCartPage
    },
    {
      path:'/five',name:'FivePage', component:FivePage
    },
    {
      path:'/shopping',name:'ShoppingPage', component:ShoppingPage
    }
  ]
})

new Vue({
  el: '#app',
  router,
  render: c => c(App)
})



