<template>
  <div class="container">
    <div class="title">
      <router-link to="/"></router-link>
      >
    </div>
    <table class="table table-hover">
      <thead>
      <tr>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
      </tr>
      </thead>
      <tbody>
      <tr v-for="list in index.shopcart">
        <td class="product">
          <input type="checkbox">
          <img :src="list.src" :alt="list.name">
          <div class="listName">{{list.name}}</div>
        </td>
        <td>
          <p>{{list.size}}</p>
          <p>{{list.color}}</p>
        </td>
        <td>{{list.price}}</td>
        <td class="count">
          <button>-</button>
          <input type="text" :value="list.count">
          <button>+</button>
        </td>
        <td>
          {{list.price*list.count}}
        </td>
        <td>
          <p></p>
          <p></p>
        </td>
      </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
  export default {
    data(){
      return{
        index: {
          "shopcart":[
            {
              "src": require("../../static/images/shopcart/01.jpg"),
              "name":"2017",
              "size":"36",
              "color":"",
              "price":"98.00",
              "count":1
            },
            {
              "src": require("../../static/images/shopcart/02.jpg"),
              "name":"",
              "size":"38",
              "color":"",
              "price":"69.00",
              "count":1
            },
            {
              "src": require("../../static/images/shopcart/03.jpg"),
              "name":"2017",
              "size":"36 9cm",
              "color":"",
              "price":"117.00",
              "count":1
            },
            {
              "src": require("../../static/images/shopcart/04.jpg"),
              "name":"insulzzanggd",
              "size":"40",
              "color":"",
              "price":"117.00",
              "count":1
            },
            {
              "src": require("../../static/images/shopcart/05.jpg"),
              "name":"2018",
              "size":"",
              "color":"",
              "price":"139.00",
              "count":1
            }
          ]
        },
      }
    },
    // created() {
    //   this.$http.get('api/index')
    //     .then((res) => {
    //       this.index = res.body.data;
    //     }, (err) => {
    //       console.log(err)
    //     })
    // }
  }
</script>

<style scoped lang="less">
  .title {
    margin-top:2rem;
  }
  .product{
    display: flex;
    justify-items: baseline;
    img{
       width: 8rem;
      height: 8rem;
     }
    .listName{
      width: 18rem;
      overflow: hidden;
    }
  }
  .count{
    input{
      width: 3rem;
      text-align: center;
    }
  }

</style>



